import { React, useState, useEffect } from 'react';
import { Document, Page, pdfjs } from 'react-pdf';
import 'react-pdf/dist/esm/Page/AnnotationLayer.css';
import 'react-pdf/dist/esm/Page/TextLayer.css';
import { FaSearchPlus, FaSearchMinus, FaUndo, FaRedo, FaArrowLeft, FaArrowRight } from 'react-icons/fa'; // Import icons
import Resume from ".././utils/KIRUTHIKA_RESUME_UP.pdf";
//import Cnt from "../CNT_WBOOK.pdf"


pdfjs.GlobalWorkerOptions.workerSrc = `//cdnjs.cloudflare.com/ajax/libs/pdf.js/${pdfjs.version}/pdf.worker.min.js`;

const Home = () => {
  const [rotation, setRotation] = useState(0);
  const [scale, setScale] = useState(1);
  const [currentPage, setCurrentPage] = useState(1);
  const [totalPages, setTotalPages] = useState(0);
  useEffect(() => {
    // Load the PDF document and retrieve total number of pages
    const loadingTask = pdfjs.getDocument(Resume);
    loadingTask.promise.then((pdf) => {
      setTotalPages(pdf.numPages);
    });
   
  }, []);
  const handleRotateLeft = () => {
    setRotation(rotation - 90);
  };

  const handleRotateRight = () => {
    setRotation(rotation + 90);
  };

  const handleZoomIn = () => {
    setScale(scale + 0.1);
  };

  const handleZoomOut = () => {
    setScale(scale - 0.1);
  };


  const handlePreviousPage = () => {
    if (currentPage > 1) {
      setCurrentPage(currentPage - 1);
    }
  };

  const handleNextPage = () => {
    if (currentPage < totalPages) {
      setCurrentPage(currentPage + 1);
    }
  };
  return (
    <div className='home'>
      <h2>PDF Viewer</h2>
      <div className='icon-container'>
        <FaSearchPlus onClick={handleZoomIn} style={{ cursor: 'pointer' }} />
        <FaSearchMinus onClick={handleZoomOut} style={{ cursor: 'pointer' }} />
        <FaUndo onClick={handleRotateLeft} style={{ cursor: 'pointer' }} />
        <FaRedo onClick={handleRotateRight} style={{ cursor: 'pointer' }} />
      </div>
      <div className='pdf-container'>
        <Document file={Resume}>
          <Page pageNumber={currentPage} rotate={rotation} scale={scale} />
        </Document>
      </div>
      <div className='icon-container'>
        <FaArrowLeft onClick={handlePreviousPage} 
          style={{ cursor: 'pointer', opacity: currentPage === 1 ? 0.5 : 1 }}
          disabled={currentPage === 1} />
        <p>{currentPage} / {totalPages}</p>
        <FaArrowRight onClick={handleNextPage} 
           style={{ cursor: 'pointer', opacity: currentPage === totalPages ? 0.5 : 1 }}
           disabled={currentPage === totalPages} />
      </div>
    </div>
  );
};

export default Home;
